___CSS_LOADER_EXPORT___.push([module.id, `/* Josh Comeau’s custom CSS reset  */
/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

/* 3. Enable keyword animations */
@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  /* 4. Add accessible line-height */
  line-height: 1.5;
  /* 5. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/* 7. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
}

/* 8. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* 9. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

/*
  10. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}

:root {
  --secondary-color: linear-gradient(
    to right top,
    #274351,
    #244153,
    #233f55,
    #233c56,
    #263957,
    #223a59,
    #1c3c5a,
    #163d5b,
    #03425a,
    #004756,
    #0b4b50,
    #1d4e4a
  );
  --shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  --light-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    "Open Sans",
    "Helvetica Neue",
    sans-serif;
}

body {
  padding: 1em;
  overflow-x: hidden;
  background-color: rgb(20, 36, 58);
  color: white;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2em;
}

.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4em;
  background-image: var(--secondary-color);
  border-radius: 14px;
  h1 {
    font-size: 3rem;
  }

  p {
    font-size: 1.2rem;
  }
}

form {
  padding: 2em 0;
  font-size: 1.3rem;

  input {
    width: 100%;
    padding: 0.4em;
    border-radius: 4px;
    border: none;
    box-shadow: var(--shadow);
    margin-bottom: 0.7em;
  }

  input:focus {
    outline: 2px solid rgb(0, 167, 130);
  }

  button {
    background-color: rgb(0, 167, 130);
    color: white;
    border: none;
    border-radius: 4px;
    padding: 0.4em;
    cursor: pointer;
  }
}

.weather-content {
  background-image: var(--secondary-color);
  min-width: 650px;
  padding: 4em;
  display: none;
  grid-template-columns: 1fr 1fr;
  border-radius: 1em;
  gap: 2em;
}

.weather-content.visible {
  display: grid;
}

.weather-content .left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 3em;
  h3 {
    font-size: 1.5rem;
  }

  .desc-img {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.2rem;
  }

  img {
    height: 7rem;
    width: 7rem;
  }

  .temperature {
    font-size: 2.5rem;
    font-weight: 700;
  }
}

.weather-content .right {
  display: grid;
  align-items: center;
  font-size: 1.2rem;
  gap: 1em;
  .info {
    display: flex;
    justify-content: space-between;
    padding: 0.5em;
    background-color: #1d344e;
  }
}

.error-message {
  color: rgb(255, 53, 53);
  font-size: 2rem;
  font-weight: 500;
}

.loader {
  width: 100px;
  height: 100px;
  border: 4px solid #fff;
  border-radius: 50%;
  display: none;
  position: relative;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  margin-top: 5rem;
}

.loader.visible {
  display: inline-block;
}

.loader::after,
.loader::before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  background: #ff3d00;
  width: 6px;
  height: 6px;
  transform: translate(150%, 150%);
  border-radius: 50%;
}
.loader::before {
  left: auto;
  top: auto;
  right: 0;
  bottom: 0;
  transform: translate(-150%, -150%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 700px) {
  html {
    font-size: 15px;
  }

  body {
    padding-top: 2em;
  }

  .weather-content {
    grid-template-columns: 1fr;
    
    min-width: 90vw;
  }
}
